* {
  box-sizing: border-box;
}
body {
  background-color: #ffffff;
  padding: 60px;
}

input {
  font-size: 1.3;
  color: black;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid rgb(44, 35, 35);
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  justify-content: center;
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  
}
